.header {
  top: 0;
  max-width: 900px;
  max-height: 25vmin;
  background-color: transparent;
  font-family: "Patrick Hand", Arial, cursive;
  font-size: 5vmin;
  font-weight: bold;
  text-transform: uppercase;
  padding-top: 15px;
  text-align: center;
  padding-bottom: 15px;
}

.headerRow {
  justify-content: space-between;
}

.headerLogo {
  float: left;
  padding-left: 15px;
}

.headerLogoBack {
  float: left;
  padding-left: 13vmin;
}

.headerLogo img {
  width: 17vmin;
}

.headerLogoBack img {
  width: 17vmin;
}

.headerPhrase {
  float: right;
  padding-right: 15px;
}

.buttonsContainer {
    box-sizing: border-box;
    size: cover;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 5vmin;
    margin-bottom: 0vmin;
}

.buttonFilterWhoWhere {
  display: flex;
  justify-content: space-around;
  background-color: white;
  box-shadow: 0 0 8px 0 #9e9e9e;
  border-radius: 1px;
  border-color: white !important;
  margin-bottom: 5vmin;
  align-items: baseline;
  min-height: 21vmin;
}
.buttonFilterWhoWhere img {
    max-width: 11vmin;
    min-height: 7vmin;
    margin: 0;
    padding: 0;
    margin-top: 0.4vmin;
}

.titleWhoWhere {
    font-size: 12vmin;
    font-family: "Questrial", Arial, sans-serif;
    font-weight: bold;
    color: #4b4b4b;
    text-align: center;
    margin-bottom: 6vmin;
}

.titleFilter {
  font-size: 5vmin;
  color: #4b4b4b;
  font-family: "Patrick Hand", Arial, cursive;
  margin: 0 auto;
}



  
